.mobile-menu-button {
  position: relative;
  z-index: 10;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 38px;
  height: 28px;
  border: none;
  padding: 0;
  cursor: pointer;
  background-color: transparent;
  transition-property: transform, background-color;
  transition-duration: 5s;
  transition-timing-function: linear;
}
.mobile-menu-button span {
  display: block;
  width: 38px;
  height: 6px;
  border-radius: 5px;
  background: #27AE60;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), background-color 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
}
.mobile-menu-button::before {
  content: "";
  display: block;
  width: 38px;
  height: 6px;
  border-radius: 5px;
  background: #27AE60;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), background-color 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
}
.mobile-menu-button::after {
  content: "";
  display: block;
  width: 38px;
  height: 6px;
  border-radius: 5px;
  background: #27AE60;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), background-color 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
}
.mobile-menu-button--menu-opened::before {
  background-color: #FFFFFF;
  transform: translateY(11px) rotate(45deg);
}
.mobile-menu-button--menu-opened span {
  background-color: transparent;
}
.mobile-menu-button--menu-opened::after {
  background-color: #FFFFFF;
  transform: translateY(-11px) rotate(-45deg);
}

.header {
