.slider-control {
  box-sizing: border-box;
  width: 33px;
  height: 33px;
  border-radius: 50%;
  background-color: #27AE60;
  border: 1px solid #27AE60;
  cursor: pointer;
  transition-duration: 0.2s;
  transition-timing-function: linear;
}
.slider-control::before {
  content: "";
  position: absolute;
  top: 12px;
  display: block;
  box-sizing: border-box;
  width: 8px;
  height: 8px;
  border-bottom: 2px solid #FFFFFF;
  border-right: 2px solid #FFFFFF;
}
.slider-control--left::before {
  left: 13px;
  transform: rotate(135deg);
}
.slider-control--right::before {
  right: 13px;
  transform: rotate(-45deg);
}
.slider-control:hover {
  border: 1px solid #27AE60;
  background-color: #FFFFFF;
}
.slider-control:hover::before {
  border-color: #27AE60;
}
.slider-control:active {
  background-color: #27AE60;
  border: 1px solid #27AE60;
}
.slider-control:active::before {
  border-color: #FFFFFF;
}
.slider-control:disabled {
  border: 1px solid #27AE60;
  background-color: transparent;
  opacity: 0.3;
}
.slider-control:disabled::before {
  border-color: #27AE60;
}
.slider-control--mobile::after {
  display: none;
}
@media (max-width: 767px) {
  .slider-control--mobile {
    border: none;
    background-color: transparent;
  }
  .slider-control--mobile::before {
    width: 18px;
    height: 18px;
    border-bottom: 4px solid #27AE60;
    border-right: 4px solid #27AE60;
  }
  .slider-control--mobile:hover, .slider-control--mobile:focus {
    background-color: transparent;
    border: none;
  }
  .slider-control--mobile:hover::before, .slider-control--mobile:focus::before {
    border-bottom: 4px solid rgba(39, 174, 96, 0.7);
    border-right: 4px solid rgba(39, 174, 96, 0.7);
  }
  .slider-control--mobile:active::before {
    border-bottom: 4px solid rgba(39, 174, 96, 0.5);
    border-right: 4px solid rgba(39, 174, 96, 0.5);
  }
}
@media (min-width: 768px) {
  .slider-control {
    width: 53px;
    height: 53px;
  }
