.overlay {
  display: block;
}

.overlay--menu-opened {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  z-index: 10;
  width: 100%;
  height: 100vh;
  background-color: #27AE60;
}
.overlay--menu-opened .leaves {
  display: none;
}

.hidden {
  display: none;
}

.leaves {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  max-width: 100%;
  height: 100vh;
  overflow: hidden;
}

.leaves__leaf {
  position: absolute;
  opacity: 0;
  top: 0;
  z-index: 0;
  animation-name: fall-and-fade;
  animation-duration: 8s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-fill-mode: backwards;
}
@media (min-width: 1560px) {
  .leaves__leaf {
    animation-name: fall-and-fade-pc;
  }
}
.leaves__leaf::before {
  content: "";
  display: block;
  background: transparent url("../../img/leaf.png") center no-repeat;
  background-size: contain;
  animation-name: spin-forward;
  animation-duration: 8s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: backwards;
  animation-iteration-count: infinite;
}
@media (min-resolution: 2dppx) {
  .leaves__leaf::before {
    background: transparent url("../../img/leaf_@2x.png") center no-repeat;
    background-size: contain;
  }
}
@media (min-width: 768px) {
  .leaves__leaf::before {
    animation-name: spin-forward;
  }
}
.leaves__leaf--huge {
  right: 10%;
  animation-delay: 0s;
}
.leaves__leaf--huge::before {
  width: 250px;
  height: 250px;
  animation-delay: 0s;
}
@media (min-width: 768px) {
  .leaves__leaf--huge {
    right: -10%;
  }
}
.leaves__leaf--big {
  left: 5%;
  animation-delay: 10s;
}
.leaves__leaf--big::before {
  width: 200px;
  height: 200px;
  animation-delay: 10s;
}
@media (min-width: 768px) {
  .leaves__leaf--big {
    left: -10%;
  }
}
.leaves__leaf--medium {
  left: 50%;
  animation-delay: 5s;
}
.leaves__leaf--medium::before {
  width: 100px;
